import React from 'react';
import './Landmark.css';




export class Landmark extends React.Component {
    constructor(props) {
        super(props);
        this.prevSlide = this.prevSlide.bind(this);
        this.nextSlide = this.nextSlide.bind(this);
    }

    prevSlide() {
        this.props.slide(-1);
    }

    nextSlide() {
        this.props.slide(1);
    }

    renderCarousel() {
        if(this.props.photo) {
            return (<div>
                <a key="prev" className="prev" href="/#" onClick={this.prevSlide}>&#10094;</a>
                <a key="next" className="next" href="/#" onClick={this.nextSlide}>&#10095;</a>
            </div>)
        }
    }
    
    render() {
        return (
            <div>
                <div className="mySlides fade">
                    <img className="landmarkPhoto" src={this.props.photo.url} alt="null"/>
                    <div className="text">{this.props.photo.countryPhoto}</div>
                    {this.renderCarousel()}
                </div>
                
            </div>
        )
    }
}



