import React from 'react';
import './Country.css';
import { ImageGallery } from '../ImageGallery/ImageGallery';




export class Country extends React.Component {
    constructor(props) {
        super(props);
        this.handleHomeButton = this.handleHomeButton.bind(this);
        
    }

    handleHomeButton() {
        this.props.backToHome();
        
    }
    
    render() {
        //formating output of languages
        const lang = this.props.language;
        const langArr = Object.values(lang);
        let newArr = [];
        for (let i = 0; i < langArr.length; i++) {
            if (!newArr.includes(' '+langArr[i])) {
                newArr.push(' ' + langArr[i]);
            }
        };
        const newString = newArr.toString();

        //formating output of population
        let population = 0;
        let unit = '';
        if (this.props.country.population > 999999) {
            population += Math.round(this.props.country.population / 1000000);
            unit += 'mln';
        } else if (this.props.country.population < 999999 && this.props.country.population > 1000) {
            population = Math.round(this.props.country.population / 1000);
            unit += 'k';
        } else {
            population = this.props.country.population;
        };

        //formating output of area
        let area = '';
        let km = '';
        if (this.props.country.square) {
            if (this.props.country.square > 999999) {
                area += Math.round(this.props.country.square / 1000000);
                km += "mln \u33A2";
            } else if (this.props.country.square < 999999 && this.props.country.square > 1000) {
                const arearaw = Math.round(this.props.country.square).toString();
                switch (arearaw.length) {
                    case 6:
                        area += arearaw.slice(0, 3) + ' ' + arearaw.slice(3, 6);
                        break;
                    case 5:
                        area += arearaw.slice(0, 2) + ' ' + arearaw.slice(2, 5);
                        break;
                    case 4:
                        area += arearaw.slice(0, 1) + ' ' + arearaw.slice(1, 3);
                        break;
                    default:
                        area += arearaw;
                        break;
                }

                km += " \u33A2";
            } else {
                area += Math.round(this.props.country.square);
                km += " \u33A2";
            };
        }


        //formating output of local time
        const temp = Math.round(this.props.temp);


        //choosing day or night weather icon
        let icon = '';
        if (this.props.weatherIcon) {
            if (this.props.isDay === true) {
                icon = `http://openweathermap.org/img/wn/${this.props.weatherIcon}@2x.png`;
            } else {
                const dIcon = this.props.weatherIcon;
                const nIcon = dIcon.slice(0, 2) + 'n';
                icon = `http://openweathermap.org/img/wn/${nIcon}@2x.png`;
            }

        };


        return (
            <div className="Country" id="country">
                <a href="/#" className="Home-Button" onClick={this.handleHomeButton}>Try again</a>
                <div className="Country-Heading">
                    <div className="Flag">
                        {this.props.flag && <img src={this.props.flag} alt="country flag" />}
                    </div>
                    <h2 key='country-name'>{this.props.country.name}</h2>

                </div>
                <div><p className="continent">{this.props.country.continent}</p></div>


                <div className="Top-Info">
                    {this.props.country.capital && <p className="Capital">Now in <span className='Capital-Text'>{this.props.country.capital}:</span> <br /><span id='time'></span></p>}

                    {this.props.temp && <p className="Weather">{temp} &#186;C</p>}
                    {icon !== '' && <img className="Weather-Icon" src={icon} alt='weather icon' />}
                    <p className="Weather-Description" >{this.props.weather}</p>


                </div>

                {this.props.photos && <ImageGallery photos={this.props.photos} />}

                <div className="Bottom-Info">
                    {this.props.country.population && <p className="Population statistic">Population:<br /> <span className="stat-span">{population}{unit}</span></p>}
                    {this.props.country.square && <p className="Square statistic">Total Area: <br /> <span className="stat-span">{area}{km}</span></p>}
                    {this.props.language && <p className="Language statistic">Language(s): <br /> <span className="stat-span">{newString}</span></p>}

                </div>

            </div>

        )
    }
};

