import React from 'react';
import './ImageGallery.css';
import { Landmark } from '../Landmark/Landmark';

let slideIndex = 1;

export class ImageGallery extends React.Component {
    constructor(props) {
        super(props);
        this.showSlides = this.showSlides.bind(this);
        this.plusSlides = this.plusSlides.bind(this);
    }

    componentDidMount(){
        if(this.props.photos[0]) {
            this.showSlides();
        }
        
    }
    componentDidUpdate(){
        if(this.props.photos[0]) {
            this.showSlides();
        }
    }

    plusSlides(param) {
        this.showSlides(slideIndex += param);
    }

    showSlides(param) {
        let i;
        const slides = document.getElementsByClassName("mySlides");
        const dots = document.getElementsByClassName("dot");
        if(param > slides.length) { slideIndex = 1};
        if (param < 1) { slideIndex = slides.length};
        for (i = 0; i < slides.length; i++) {
            slides[i].style.display = "none";
        };
        for (i = 0; i < dots.length; i++) {
            dots[i].className = dots[i].className.replace(" active", "");
        };
        slides[slideIndex-1].style.display = "block";
        dots[slideIndex-1].className += " active";
    }

    render() {
        
        if(this.props.photos !== undefined) {
        return (
            <div className="Landmarks-gallery">
                
                {this.props.photos.map(photo => {
                    return <Landmark photo={photo} slide={this.plusSlides} key={photo.id} />
                })}
                
                <div className="dotsContainer">
                    <span key="1" className="dot" ></span>
                    <span key="2" className="dot" ></span>
                    <span key="3" className="dot" ></span>
                </div>
            </div>
        )} else {
            return (
                <h3>Top 3 Landmarks</h3>
            )
        }
    }
}